<template>
  <v-container
    id="data-tables"
    class="fill-height align-start"
    fluid
    tag="section"
  >
    <v-container
      fluid
    >
      <ik-data-table
        :use-default-action="false"
        :model="model"
        :filter="filter"
        :entity-name="$t('全部账单')"
        :use-select="false"
        :icon="'mdi-format-list-bulleted'"
      >
        <template v-slot:filterLeft>
          <v-menu
            ref="menu"
            v-model="menu"
            :close-on-content-click="false"
            :return-value.sync="dates"
            offset-y
          >
            <template v-slot:activator="{ on }">
              <v-text-field
                v-model="dates"
                class="ml-auto"
                hide-details
                :label="$t('日期筛选')"
                prepend-icon="mdi-calendar"
                readonly
                style="max-width: 250px;"
                single-line
                append-icon="mdi-close"
                @click:append="clear"
                v-on="on"
              />
            </template>
            <v-date-picker
              v-model="dates"
              no-title
              range
              scrollable
              locale="de"
            >
              <v-spacer/>
              <v-btn
                text
                color="primary"
                @click="menu = false"
              >
                {{ $t('Cancel') }}
              </v-btn>
              <v-btn
                text
                color="primary"
                @click="$refs.menu.save(dates)"
              >
                {{ $t('OK') }}
              </v-btn>
            </v-date-picker>
          </v-menu>
        </template>
        <template v-slot:filterRight="{ tableItems }">
          <v-btn
            class="ml-4"
            color="#367aeb"
          >
            {{ $t('totalPrice') }}：€{{ calculateTotalPrice(tableItems).toFixed(2) }}
          </v-btn>
        </template>
        <template v-slot:footer>
          <v-toolbar
            color="white"
            flat
          >
            <template v-for="(n, i) in billSelectionList">
              <v-btn
                :key="i"
                class="mr-1"
                :color="billSelectionColorList[i]"
                dark
                outlined
                @click="setBillDates(i)"
              >
                {{ n }}
              </v-btn>
            </template>

            <v-spacer/>
            <v-btn
              class="mb-2"
              color="primary"
              dark
              @click="print()"
            >
              {{ $t('打印') }}
            </v-btn>
          </v-toolbar>
        </template>
        <template v-slot:item.action="{ item }">
          <v-icon
            class="mr-2"
            small
            @click="goNext(item)"
          >
            mdi-arrow-right
          </v-icon>
        </template>
      </ik-data-table>
    </v-container>
  </v-container>
</template>

<script>

  import { PrinterAllSummary } from '../../components/Utlis/api'
  import Utils from '../../components/Utlis/Utils'
  import HistoryBills from '../../model/HistoryBills'
  import { IkDataTable } from 'innerken-utils'
  import moment from 'moment'

  export default {
    components: { IkDataTable },
    data: function () {
      return ({
        model: HistoryBills,
        dates: [],
        timer: null,
        menu: false,
        modal: false,
        menu2: false,
        billSelectionList: [
          this.$i18n.t('当日结单'),
          this.$i18n.t('本周结单'),
          this.$i18n.t('本月结单'),
          this.$i18n.t('本年结单'),
        ],
        billSelectionColorList: ['green darken-2', 'blue darken-2', 'purple darken-2', 'pink'],
      })
    },
    computed: {
      filter () {
        if (Date.parse(this.dates[0]) > Date.parse(this.dates[1])) {
          [this.dates[0], this.dates[1]] = [this.dates[1], this.dates[0]]
        }
        return { timespan: this.dates ? this.dates.join(' - ') : '' }
      },
    },

    watch: {
    },
    created () {
      if (localStorage.getItem('selectData').length <= 2) {
        this.dates = [this.formatDate(-7), this.formatDate(0)]
      } else {
        this.dates = JSON.parse(localStorage.getItem('selectData'))
      }
    },
    methods: {
      calculateTotalPrice (items) {
        return items ? items.reduce(
          (arr, item) =>
            item.consumeTypeStatusId !== 5 ? arr + parseFloat(item.totalPrice) : arr
          , 0) : 0
      },
      formatDate (num) {
        var now = new Date()
        var nowTime = now.getTime()
        var oneDayTime = 24 * 60 * 60 * 1000
        var ShowTime = nowTime + num * oneDayTime
        var myDate = new Date(ShowTime)
        var y = myDate.getFullYear()
        var m = myDate.getMonth() + 1
        var d = myDate.getDate()
        console.log('======' + [y, m, d].join('-'))
        return [y, m, d].join('-')
      },
      goNext (item) {
        localStorage.setItem('selectData', JSON.stringify(this.dates))
        this.$router.push({
          name: 'Seite mit den Rechnungsdetails',
          params: { id: item.orderId },
        })
      },
      clear () {
        this.dates = []
        localStorage.setItem('selectData', JSON.stringify(this.dates))
      },
      setBillDates (i) {
        switch (i) {
          case 0:
            this.dates = [this.formatDate(0), this.formatDate(0)]
            break
          case 1:
            this.dates = [moment().startOf('week').format('YYYY-MM-DD'), this.formatDate(0)]
            break
          case 2:
            this.dates = [moment().startOf('month').format('YYYY-MM-DD'), this.formatDate(0)]
            break
          case 3:
            this.dates = [moment().startOf('year').format('YYYY-MM-DD'), this.formatDate(0)]
        }
      },
      print () {
        Utils.showConfirm('Bist du sicher?', 'Möchten Sie alle Datensätze drucken?', () => {
          PrinterAllSummary(this.filter).then(() => {
            Utils.toast('Erfolgreich drucken!')
          })
        })
      },
    },
  }
</script>
